* {
    box-sizing: border-box;
  }
  
  a {
    display: block;
    text-decoration: none;
  }
  
  button {
    padding: 0;
    margin: 0;
    cursor: pointer;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  p {
    margin: 0;
  }
  
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }